import * as React from 'react';
import { IconButton } from './Basics';
import type { IIconButtonProps } from './Basics';
import { IconBrush } from '../icons';

export function BrushButton(props: IIconButtonProps) {
  const {
    icon = <IconBrush />,
    ...rest
  } = props;

  return (
    <IconButton
      testid='brush_button'
      icon={icon}
      {...rest}
    />
  )
}